<template>
  <div class="third-step">
    <div class="content">
      <div class="table-info">
        <span>综合评估风险矩阵设置</span>
      </div>
      <div class="content-detail">
        <table class="first-tab" cellspacing="0" cellpadding="0">
          <tr v-for="(tabledata,index) in tabledatas2" :key="index">
            <td v-for="(tddata,tdIndex) in tabledata" :style="{'background':tddata.color}">
              <template v-if="index < tabledatas2.length-1">
                <template v-if="tdIndex==0">
                      {{tddata.levelCode}}
                </template>
                <template v-else>
                    <Icon type="ios-arrow-back" @click="backStep(tddata)" v-if="tddata.backStepFlag != false"/>
                      {{tddata.levelCode}}
                    <Icon type="ios-arrow-forward"  @click="forwardStep(tddata)" v-if="tddata.forwardStepFlag != false"/>
                </template>
              </template>
              <template v-else>
                <template v-if="tdIndex==0">
                  <template v-for="(item,index1) in tddata.levelCode">
                    <div>{{item}}</div>
                    <p v-if="index1 < 1"></p>
                  </template>
                </template>
                <template v-else>
                    {{tddata.levelCode}}
                </template>
              </template>
            </td>
          </tr>
        </table>
      </div>
      <div class="form">
          <Button type="primary" @click="save">保存</Button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
      this.axios
      .get("/model/orgRisklevel/getRiskLevelView")
      .then(response => {
        if (response.code == "200") {
            let data = response.data.orgLevelSumList;
            this.orgLevelCompList = JSON.parse(JSON.stringify(response.data.orgLevelCompList));
            let forwCode = data[0].levelCode1;
            let arraTemp = [];
            let lastArraTemp = [];
            lastArraTemp.push({
               levelCode: ["固有风险", "控制措施有效性"], 
               color: "" 
            });
            arraTemp.push({
              levelCode: data[0].levelName1,
              color: ""
            });
            let flag = true;
            for(let i=0; i<data.length; i++) {
                let index = 1;
                this.orgLevelSumList.push({
                  levelCode: data[i].levelCode,
                  levelCode1: data[i].levelCode1,
                  levelCode2: data[i].levelCode2,
                });
                this.orgLevelCompList.forEach((element, ind) => {
                    if (data[i].levelCode == element.levelCode) {
                        index = ind;
                    }
                });
                if (index == 0) data[i].backStepFlag = false;
                if (index ==this.orgLevelCompList.length-1) data[i].forwardStepFlag = false;
                if (data[i].levelCode1 == forwCode) {
                    arraTemp.push(data[i]);
                    if (flag) 
                      lastArraTemp.push({
                        levelCode: data[i].levelName2, 
                        color: "" 
                      });
                }else {
                  forwCode = data[i].levelCode1;
                  flag = false;
                  this.tabledatas2.push(JSON.parse(JSON.stringify(arraTemp)));
                  arraTemp = [];
                  arraTemp.push(
                    {
                      levelCode: data[i].levelName1,
                      color: ""
                    }
                  );
                   arraTemp.push(data[i]);
                }
            }
            this.tabledatas2.push(JSON.parse(JSON.stringify(arraTemp)));
            this.tabledatas2.push(lastArraTemp);
        }
      })
      .catch(error => {});
  },
  data() {
    return {
      tabledatas2: [],
      orgLevelSumList: [],
      orgLevelCompList: [],
    };
  },
  methods: {
    backStep(item) {
      let index = 1;
      this.orgLevelCompList.forEach((element, ind) => {
          if (item.levelCode == element.levelCode) {
              index = ind;
              return;
          }
      });
      if (index == 0) {item.forwardStepFlag = true;return;}
      else if (index == 1) {item.forwardStepFlag = true;item.backStepFlag = false;}
      else {item.forwardStepFlag = true;item.backStepFlag = true;}
      item.levelCode = this.orgLevelCompList[index-1].levelCode;
      item.color = this.orgLevelCompList[index-1].color;
      this.orgLevelSumList.forEach(element => {
        if (element.levelCode1 == item.levelCode1 && element.levelCode2 == item.levelCode2) {
            element.levelCode = item.levelCode;
            return;
        }
      });
    },
    forwardStep(item) {
      let index = this.orgLevelCompList.length-2;
      this.orgLevelCompList.forEach((element, ind) => {
          if (item.levelCode == element.levelCode) {
              index = ind;
              return;
          }
      });
      if (index == this.orgLevelCompList.length-1) {item.backStepFlag = true;return;}
      else if (index == this.orgLevelCompList.length-2) {item.backStepFlag = true;item.forwardStepFlag = false;}
      else {item.backStepFlag = true;item.forwardStepFlag = true};
      item.levelCode = this.orgLevelCompList[index+1].levelCode;
      item.color = this.orgLevelCompList[index+1].color;
      this.orgLevelSumList.forEach(element => {
        if (element.levelCode1 == item.levelCode1 && element.levelCode2 == item.levelCode2) {
            element.levelCode = item.levelCode;
            return;
        }
      });
    },
    save(){
        let data = JSON.stringify({
            orgRiskLevelSumDto: this.orgLevelSumList
        });
        this.axios
        .post("/model/orgRisklevel/saveRiskLevel", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.$Message.success("保存成功");
          }
        })
        .catch(error => {});
    }
  }
};
</script>
<style lang="scss" scoped>
.third-step {
  .content {
    width: 1000px;
    .table-info {
      font-size: 14px;
      margin: 20px auto 40px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .tab-desc {
      color: #fff;
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      background: #019fe8;
      padding-left: 20px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .content-detail {
      background: #fff;
      padding: 50px 100px;
      font-size: 16px;
      border: 1px solid #d9d9d9;
      .first-tab {
        width: 100%;
        border: 1px solid #797979;
        margin-top: 15px;
        td,
        th {
          border: 0.5px solid #797979;
          height: 40px;
          line-height: 40px;
          text-align: center;
          width: 100px;
          position: relative;
          p {
            width: 159px;
            height: 1px;
            background: #797979;
            transform: rotate(146deg);
            position: absolute;
            left: -13px;
            top: 44px;
          }
          div {
            &:first-child {
              position: relative;
              top: -1px;
              left: -20px;
            }
            &:nth-child(3) {
              width: 70px;
              position: relative;
              right: -60px;
              line-height: 20px;
              margin: 5px 0;
              text-align: left;
            }
          }
          &.td-scale{
            transform: scale(1.1);
            z-index: 2;
            font-size: 20px;
            font-weight: bold;
          }
        }
      }
      .title-content {
        line-height: 40px;
      }
    }
    .form{
        margin-top: 25px;
    }
  }
}
</style>


